body {
  font-family: 'arial';
  background-color: #99bdf5;
  padding: 30px;
  color: #333;
}

.App {

  max-width: 600px;
  margin: 0 auto;
  background-color: #efefef;
  padding: 20px 30px;
  border-radius: 10px;
  margin-bottom: 300px;
}

h1 {

  text-align: center;
}

h2 {
  margin-bottom: 10px;
}

p {
  margin: 10px;
}

input {
  box-sizing: border-box;
}

button {

  background-color: #333974;
  color: #fff;
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  opacity: 0.8;
  transition: 0.3s;
  margin-right: 10px;
}

button:hover{
  opacity: 1;
}

.complete{
  background-color: #5cb85c;
}

.remove{
  background-color: #d9534f;
}

.todo-form{

  padding-bottom: 1.5rem;
}

.todo-list{

  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #555;

}

.todo{
  background-color: white;
  box-shadow: 0 0 10 rgba(0,0,0,0.1);
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

input, select{

  padding: 5px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 3px;
  width: 100%;
}

.filter, .search{

  border-bottom: 1px solid #555;
  margin-bottom: 20px;
  padding-bottom: 20px;

}

.filter-options{
  display: flex;
  justify-content: space-between;
  gap: 3rem;
}

.filter-options div{

   flex: 1 1 0;
}